//@ts-check

import { MobJs } from '@mobJs';
import { detectFirefox, detectSafari } from '@utils/utils';
import { CaterpillarN0Fn } from './caterpillar-n0';
import { AnimationTitle } from '@commonComponent/animation-title/definition';

/**
 * @import {CreateComponentParams} from "@mobJsType";
 */

export const CaterpillarN0 = MobJs.createComponent(
    /** @type {CreateComponentParams<import('./type').CaterpillarN0>} */
    ({
        tag: 'caterpillar-n0',
        component: CaterpillarN0Fn,
        exportState: [
            'nextRoute',
            'prevRoute',
            'amountOfPath',
            'width',
            'height',
            'radius',
            'fill',
            'stroke',
            'opacity',
            'spacerY',
            'intialRotation',
            'perpetualRatio',
            'mouseMoveRatio',
            'disableOffcanvas',
        ],
        state: {
            isMounted: false,
            nextRoute: () => ({
                value: '',
                type: String,
            }),
            prevRoute: () => ({
                value: '',
                type: String,
            }),
            amountOfPath: 17,
            width: window.innerHeight / 30,
            height: window.innerHeight / 30,
            radius: 0,
            fill: [-1],
            stroke: '#000',
            opacity: 0.05,
            spacerY: (/** @type {any} */ condition) => (condition ? 300 : -400),
            intialRotation: 33,
            perpetualRatio: 6,
            mouseMoveRatio: 10,
            disableOffcanvas: () => ({
                value: detectFirefox() || detectSafari() ? true : false,
                type: Boolean,
            }),
        },
        child: [AnimationTitle],
    })
);
